import { _decorator, Component, Node, Prefab } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('ReelController')
export class ReelController extends Component {


    @property({type: Prefab})
    protected stops: Prefab[] = [];



    private _firstDelay: number = 0;
    private _count: number = 0;

    start() {

    }

    setStartDelay(delay) {
        this._firstDelay = delay;
    }

    setCount(count) {
        this._count = count;
    }

    moveNextStatus() {

        var len = this.node.childrenCount;
        var self = this;
        for (var i = 0; i < len; i ++) {
            var pos = this.node.children[i].position;

            if (i == len - 1) {
                cc.tween(this.node.children[i])
                    .delay(this._firstDelay)
                    .to(0.1, { position: cc.v3(0, pos.y-76, 0) })
                    .call(() => {
                        self.movingFinished();
                    })
                .start();
            } else {
                cc.tween(this.node.children[i])
                    .to(0.1, { position: cc.v3(0, pos.y-76, 0) })
                .start();
            }
        }

        this._firstDelay = 0;
    }

    movingFinished() {

        var len = this.node.childrenCount;
        for (var i = 0; i < len; i ++) {
            var pos = this.node.children[i].position;
            console.log (pos);
            if(pos.y < -10)  this.node.children[i].setPosition(cc.v3(0, (len-1) * 76, 0));
        }
        this._count --;
        if(this._count > 0)
            this.moveNextStatus();
    }

}

